<template>
  <div class="activity">
    <div class="detailTop">
      <img
        class="back"
        @click="$router.push({ path: '/detail' })"
        src="@/assets/phone/AppVisitorHome/back.png"
        alt
      />
      <div>报名</div>
      <img
        class="My"
        @click="$router.push('/my')"
        src="@/assets/phone/APP_activity/my.png"
        alt
      />
    </div>
    <div class="detailContent">
      <div class="detailTitle">报名信息</div>
      <ContentBox
        :allNum="allNum[index]"
        @flagFn="flagFn"
        @addData="addData"
        @deleteData="deleteData"
        @clickFn="getData"
        v-for="(item, index) in objList"
        :formData="item"
        :key="allNum[index]"
      ></ContentBox>
      <div class="detailBom">
        <div class="MandatoryBox">
          <div class="MandatoryBoxItem">
            <input type="checkbox" v-model="checkVal" name id />
            <div>
              我已阅读
              <span>《活动须知》</span>，并保证以上填写内容正确属实
            </div>
          </div>
          <ul class="color8">
            <li>安全提醒</li>
            <li>疫情期间请自觉遵守各项安全防控规定。参观前请出示健康码。</li>
          </ul>
        </div>
        <div class="SubmitBtn" @click="SubmitBtn">提&nbsp;&nbsp;&nbsp;交</div>
      </div>
    </div>
    <dialog name="deleteDio" />
  </div>
</template>

<script>
import ContentBox from "./contentBox.vue";
import {
  validatorPhone,
  validatorIDcard,
  positiveIntegerNumber,
} from "@/utils/validate";
import { addEventReservation } from "@/api/data/reservation";
import {
  getActivityList,
  getActivityPeopleNumber,
  deleteActivityRecruit,
} from "@/api/data/reservation";
import { Dialog } from "vant";
export default {
  components: { ContentBox, Dialog },
  data() {
    return {
      allNum: [],
      checkVal: false,
      objList: [],
      formRules: {
        recruitType: "身份类型",
        company: "工作单位",
        recruitName: "姓名",
        telephone: "联系方式",
        idNumber: "身份证号",
        city: "所在城市",
        isOverseas: "是否境外",
        num: "数量",
        recruitMethod: "报名方式",
      },
      activityId: JSON.parse(this.$store.state.activityView.activity).uuid,
    };
  },
  methods: {
    addData(val) {
      if (this.objList.length == 5) {
        this.$message.warning("已经是最大数量");
        return;
      } else {
        this.objList.splice(
          this.allNum.findIndex((c) => c == val.index) + 1,
          0,
          {
            recruitType: "",
            company: "",
            recruitName: "",
            telephone: "",
            idNumber: "",
            city: "",
            isOverseas: "",
            recruitMethod: "",
            age: "",
            num: 1,
            activityId: this.activityId,
          }
        );
        this.allNum.splice(
          this.allNum.findIndex((c) => c == val.index) + 1,
          0,
          new Date().getTime() +
            "-" +
            new Date().getMilliseconds() +
            "-" +
            Math.round(Math.random() * 10000)
        );
      }
    },
    deleteData(val) {
      let _this = this;
      if (val.id) {
        Dialog.confirm({
          title: "提示",
          message: "是否删除本条数据?",
        })
          .then(() => {
            deleteActivityRecruit(val.id).then((res) => {
              _this.$message.success("删除成功");
              _this.objList.splice(
                _this.allNum.findIndex((c) => c == val.index),
                1
              );
              _this.allNum.splice(
                _this.allNum.findIndex((c) => c == val.index),
                1
              );
              if (_this.objList.length == 0) {
                _this.objList.push({
                  recruitType: "",
                  company: "",
                  recruitName: "",
                  telephone: "",
                  idNumber: "",
                  city: "",
                  isOverseas: "",
                  recruitMethod: "",
                  age: "",
                  num: 1,
                  activityId: _this.activityId,
                });
                _this.allNum.push(
                  new Date().getTime() +
                    "-" +
                    new Date().getMilliseconds() +
                    "-" +
                    Math.round(Math.random() * 10000)
                );
              }
            });
          })
          .catch(() => {
            // on cancel
          });
      } else {
        this.objList.splice(
          this.allNum.findIndex((c) => c == val.index),
          1
        );
        this.allNum.splice(
          this.allNum.findIndex((c) => c == val.index),
          1
        );
        if (this.objList.length == 0) {
          this.objList.push({
            recruitType: "",
            company: "",
            recruitName: "",
            telephone: "",
            idNumber: "",
            city: "",
            isOverseas: "",
            recruitMethod: "",
            age: "",
            num: 1,
            activityId: this.activityId,
          });
          this.allNum.push(
            new Date().getTime() +
              "-" +
              new Date().getMilliseconds() +
              "-" +
              Math.round(Math.random() * 10000)
          );
        }
      }
    },
    flagFn(obj) {
      console.log(obj);
    },
    getData(val) {
      this.objList.map((item, index) => {
        if (val.allNum == this.allNum[index]) {
          item[val.name] = val.val;
          // this.objList.splice(index, 1, val.val);
        }
      });
    },
    getRecruitList() {
      let obj = {
        activityId: JSON.parse(this.$store.state.activityView.activity).uuid,
      };
      let _this = this;
      _this.objList = [];
      _this.allNum = [];
      getActivityList(obj).then((res) => {
        if (res.data.length == 0) {
          _this.objList.push({
            recruitType: "",
            company: "",
            recruitName: "",
            telephone: "",
            idNumber: "",
            city: "",
            isOverseas: "",
            recruitMethod: "",
            age: "",
            num: 1,
            activityId: this.activityId,
          });
          _this.allNum.push(
            new Date().getTime() +
              "-" +
              new Date().getMilliseconds() +
              "-" +
              Math.round(Math.random() * 10000)
          );
        } else {
          res.data.forEach((item) => {
            _this.objList.push(item);
            _this.allNum.push(
              new Date().getTime() +
                "-" +
                new Date().getMilliseconds() +
                "-" +
                Math.round(Math.random() * 10000)
            );
          });
        }
      });
    },
    SubmitBtn() {
      let flag = true;
      let allNum_ = 0;
      let allNum_uu = 0;
      let addList = [];
      if (this.checkVal) {
        this.objList.forEach((item, index) => {
          if (flag) {
            for (let typeData in item) {
              if (typeData == "telephone") {
                if (!validatorPhone(item[typeData]).flag) {
                  this.$message({
                    type: "warning",
                    message:
                      "第" +
                      (index + 1) +
                      "个报名表" +
                      validatorPhone(item[typeData]).message,
                  });
                  flag = false;
                  return;
                }
              } else if (typeData == "idNumber") {
                if (!validatorIDcard(item[typeData]).flag) {
                  this.$message({
                    type: "warning",
                    message:
                      "第" +
                      (index + 1) +
                      "个报名表" +
                      validatorIDcard(item[typeData]).message,
                  });
                  flag = false;
                  return;
                }
              } else if (typeData == "num") {
                if (!positiveIntegerNumber(item[typeData]).flag) {
                  this.$message({
                    type: "warning",
                    message: "第" + (index + 1) + "个报名表,请输入访客数量",
                  });
                  flag = false;
                  return;
                }
              } else {
                if (item[typeData] == "" || item[typeData] == null) {
                  if (this.formRules[typeData]) {
                    this.$message({
                      type: "warning",
                      message:
                        "第" +
                        (index + 1) +
                        "个报名表" +
                        this.formRules[typeData] +
                        "未填写",
                    });
                    flag = false;
                    return;
                  }
                }
              }
            }
            allNum_ += parseInt(item.num);
            if (item.uuid) {
              allNum_uu += parseInt(item.num);
            } else {
              addList.push(item);
            }
          }
        });

        if (flag) {
          console.log(this.objList);
          if (addList.length != 0) {
            let _this = this;
            let idNumberCount = true;
            addList.forEach((c) => {
              if (
                _this.objList.filter((b) => b.idNumber == c.idNumber).length > 1
              ) {
                idNumberCount = false;
              }
            });
            if (idNumberCount) {
              getActivityPeopleNumber({
                id: this.activityId,
              }).then((res) => {
                if (allNum_uu + res.data < allNum_) {
                  _this.$message.warning(
                    "您的报名人数已超出活动剩余可报名名额!剩余名额:" + res.data
                  );
                } else {
                  addEventReservation(addList).then((res) => {
                    _this.$message.success(res.data.msg);
                    _this.getRecruitList();
                  });
                }
              });
            } else {
              this.$message.warning("身份证号重复,一个身份证号只能预约一次!");
            }
          }
        }
      } else {
        this.$message.warning("请勾选《活动须知》");
        return;
      }
    },
  },
  mounted() {
    this.getRecruitList();
  },
};
</script>
<style lang='scss' scoped>
.activity {
  width: 100vw;
  height: 100vh;
  background-color: #f9f9f9;
  .detailTop {
    width: 100vw;
    height: 9vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 3vw;
    .back {
      width: 7vw;
      height: 7vw;
    }
    .My {
      width: 7vw;
      height: 7vw;
    }
    div {
      font-size: 5vw;
      color: white;
    }
  }
  .detailContent {
    width: 94vw;
    height: 85vh;
    overflow-y: scroll;
    background-color: white;
    margin: 5vw auto 0;
    box-sizing: border-box;
    padding: 3vw 1vw;
    border-radius: 3vw;
    &::-webkit-scrollbar {
      display: none;
    }
    .detailTitle {
      font-size: 4.4vw;
      font-weight: 600;
      color: #484848;
      padding-left: 3vw;
      position: relative;
      margin-left: 1vw;
      &::before {
        content: " ";
        position: absolute;
        height: 70%;
        width: 2px;
        top: 15%;
        left: 0;
        background-color: #484848;
      }
    }
    .detailBom {
      margin-top: 5vw;
      .MandatoryBox {
        margin: 3vw 0 0 0;
        font-size: 3.6vw;
        align-content: center;
        .MandatoryBoxItem {
          display: flex;
          input {
            margin: 0.6vw 3vw 0 2vw;
          }
          div {
            color: #cacaca;
            font-size: 3vw;
            span {
              color: #57a5ca;
              border-bottom: 0.5vw #3099ff solid;
            }
          }
        }
        .color8 {
          font-size: 3vw;
          color: #868786;
          list-style: none;
          margin: 2vw 0 0 1.8vw;
        }
      }
      .SubmitBtn {
        text-align: center;
        height: 6vh;
        line-height: 6vh;
        width: 90vw;
        font-size: 4vw;
        font-weight: 400;
        margin: 4vw auto 0;
        background-color: #15cfa4e5;
        border: 1px solid #97979771;
        color: white;
        border-radius: 1vw;
      }
    }
  }
}
</style>      