<template>
  <div>
    <div class="contentBox">
      <div class="detailIcon">
        <div>
          <img @click="deleteData" src="@/assets/phone/delete.png" alt />
          <img @click="addData" src="@/assets/phone/add.png" alt />
        </div>
      </div>
      <div
        class="appHomeContent"
        :style="{ pointerEvents: form.uuid ? 'none' : 'unset' }"
      >
        <div class="contentItem">
          <div class="itemLabel">身份类型</div>
          <div class="itemValue w77">
            <div class="AddDate" @click="showPopFn(1)">
              {{ recruitTypeName }}
            </div>
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">工作单位</div>
          <div class="itemValue w77">
            <input type="text" v-model="form.company" placeholder />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">姓名</div>
          <div class="itemValue w77">
            <input type="text" v-model="form.recruitName" placeholder />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">联系方式</div>
          <div class="itemValue w77">
            <input
              type="text"
              v-model="form.telephone"
              @blur="blurPhone(form.telephone)"
              placeholder
            />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">身份证号</div>
          <div class="itemValue w77">
            <input
              type="text"
              v-model="form.idNumber"
              @blur="blurID(form.idNumber)"
              placeholder
            />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">所在城市</div>
          <div class="itemValue w77">
            <input type="text" v-model="form.city" placeholder />
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">是否境外</div>
          <div class="itemValue w77">
            <div class="AddDate" @click="showPopFn(2)">
              {{ isOverseasName }}
            </div>
          </div>
        </div>
        <div class="contentItem">
          <div class="itemLabel">报名方式</div>
          <div class="itemValue w77">
            <div class="AddDate" @click="showPopFn(3)">
              {{ recruitMethodName }}
            </div>
          </div>
        </div>
        <div class="contentItem itemLast" v-if="recruitMethodName != '个人'">
          <div class="itemLabel">数量</div>
          <div class="itemValue w77">
            <input
              type="text"
              v-model="form.num"
              @blur="positiveIntegerNumber_(form.num)"
              placeholder
            />
          </div>
        </div>
      </div>
      <van-popup
        v-model="optionsFlag"
        position="bottom"
        :style="{ height: '40%' }"
      >
        <van-picker
          show-toolbar
          :value-key="optionsField.text"
          :columns="pickerList"
          :columns-field-names="optionsField"
          @confirm="onConfirm"
          @cancel="onCancel"
        />
      </van-popup>
    </div>
  </div>
</template>

<script>
// import { cellGroup, form } from "vant";
import { positiveIntegerNumber } from "@/utils/validate";
export default {
  components: {
    // "van-cell-group": cellGroup,
    // "van-form": form,
  },
  props: ["allNum", "formData"],
  data() {
    // const onFailed = (errorInfo) => {
    //   console.log("failed", errorInfo);
    // };
    return {
      recruitTypeName: "",
      isOverseasName: "",
      recruitMethodName: "",
      optionsFlag: false,
      form: {
        recruitType: "",
        company: "",
        recruitName: "",
        telephone: "",
        idNumber: "",
        city: "",
        isOverseas: "",
        recruitMethod: "", //报名方式
        age: "", //年龄
        num: 1, //数量
        visitorId:
          window.sessionStorage.getItem("SET_TOKEN") ||
          this.$store.state.user.token,
        activityId: JSON.parse(this.$store.state.activityView.activity).uuid,
      },
      recruitTypeList: [], //身份类型
      isOverseasList: [], //是否境外
      recruitMethodList: [], //报名方式
      optionsField: {
        text: "dictLabel",
        values: "dictValue",
      },
      pickerList: [],
      showPickType: "",
      changeFlag: false, //初始化下拉框数据不回传
    };
  },
  watch: {
    "form.recruitType"(val) {
      this.recruitTypeName = this.getDictLabel(val, "recruitTypeList");
      if (this.changeFlag) {
        this.changePro(val, "recruitType");
      }
    },
    "form.isOverseas"(val) {
      this.isOverseasName = this.getDictLabel(val, "isOverseasList");
      if (this.changeFlag) {
        this.changePro(val, "isOverseas");
      }
    },
    "form.recruitMethod"(val) {
      this.recruitMethodName = this.getDictLabel(val, "recruitMethodList");

      if (val == 1) {
        this.form.num = 1;
      }
      if (this.changeFlag) {
        this.changePro(val, "recruitMethod");
      }
    },
    "form.idNumber"(val) {
      this.form.age = this.getAge(val);

      this.changePro(val, "idNumber");
    },
    "form.company"(val) {
      this.changePro(val, "company");
    },
    "form.recruitName"(val) {
      this.changePro(val, "recruitName");
    },
    "form.telephone"(val) {
      this.changePro(val, "telephone");
    },
    "form.city"(val) {
      this.changePro(val, "city");
    },
    "form.age"(val) {
      this.changePro(val, "age");
    },
    "form.num"(val) {
      this.changePro(val, "num");
    },
  },
  methods: {
    changePro(val, name) {
      let valFlag = {
        val: val,
        name: name,
        allNum: this.allNum,
      };
      this.$emit("clickFn", valFlag);
    },
    getAge(cardNum) {
      let age = 0;
      let myDate = new Date();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      if (this.blurID_(cardNum)) {
        if (cardNum.length == 18) {
          //18位身份证号处理
          age = myDate.getFullYear() - cardNum.substring(6, 10) - 1;
          if (
            cardNum.substring(10, 12) < month ||
            (cardNum.substring(10, 12) == month &&
              cardNum.substring(12, 14) <= day)
          ) {
            age++;
          }
        } else if (cardNum.length == 15) {
          //15位身份证号处理

          age =
            myDate.getFullYear() - parseInt("19" + cardNum.substring(6, 8)) - 1;
          if (
            cardNum.substring(8, 10) < month ||
            (cardNum.substring(8, 10) == month &&
              cardNum.substring(10, 12) <= day)
          ) {
            age++;
          }
        }
      }
      return age;
    },
    getDictLabel(val, name) {
      return this[name].find((c) => c.dictValue == val).dictLabel;
    },
    addData() {
      this.$emit("addData", {
        index: this.allNum,
      });
    },
    deleteData() {
      this.$emit("deleteData", {
        id: this.form.uuid,
        index: this.allNum,
      });
    },
    blurPhone(val) {
      let sj =
        /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/;
      if (!sj.test(val)) {
        this.$message.warning("手机号不正确");
        return false;
      } else {
        return true;
      }
    },
    blurID(val) {
      let sfz =
        /^\d{6}((((((19|20)\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|(((19|20)\d{2})(0[13578]|1[02])31)|((19|20)\d{2})02(0[1-9]|1\d|2[0-8])|((((19|20)([13579][26]|[2468][048]|0[48]))|(2000))0229))\d{3})|((((\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|((\d{2})(0[13578]|1[02])31)|((\d{2})02(0[1-9]|1\d|2[0-8]))|(([13579][26]|[2468][048]|0[048])0229))\d{2}))(\d|X|x)$/;
      if (!sfz.test(val)) {
        this.$message.warning("身份证不正确");
        return false;
      } else {
        return true;
      }
    },
    blurID_(val) {
      let sfz =
        /^\d{6}((((((19|20)\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|(((19|20)\d{2})(0[13578]|1[02])31)|((19|20)\d{2})02(0[1-9]|1\d|2[0-8])|((((19|20)([13579][26]|[2468][048]|0[48]))|(2000))0229))\d{3})|((((\d{2})(0[13-9]|1[012])(0[1-9]|[12]\d|30))|((\d{2})(0[13578]|1[02])31)|((\d{2})02(0[1-9]|1\d|2[0-8]))|(([13579][26]|[2468][048]|0[048])0229))\d{2}))(\d|X|x)$/;
      if (!sfz.test(val)) {
        return false;
      } else {
        return true;
      }
    },
    positiveIntegerNumber_(val) {
      if (!positiveIntegerNumber(val).flag) {
        this.$message.warning("请输入访客数量!");
      }
    },
    showPopFn(flag) {
      this.optionsFlag = true;
      this.showPickType = flag;
      if (flag == 1) {
        this.pickerList = this.recruitTypeList;
      } else if (flag == 2) {
        this.pickerList = this.isOverseasList;
      } else {
        this.pickerList = this.recruitMethodList;
      }
    },
    onConfirm(value, index) {
      this.optionsFlag = false;
      if (this.showPickType == 1) {
        this.form.recruitType = value.dictValue;
      } else if (this.showPickType == 2) {
        this.form.isOverseas = value.dictValue;
      } else {
        this.form.recruitMethod = value.dictValue;
      }
    },
    onCancel() {
      this.optionsFlag = false;
    },
  },

  created: async function () {
    let _this = this;
    await this.dictType("masses_type").then((res) => {
      _this.recruitTypeList = res;
    });
    await this.dictType("is_electronic").then((res) => {
      _this.isOverseasList = res;
    });
    await this.dictType("registration_method").then((res) => {
      _this.recruitMethodList = res;
    });
    if (this.formData.uuid) {
      this.form = Object.assign({}, this.formData);
    }
    this.changeFlag = true;

    this.$nextTick(() => {
      if (!_this.form.uuid) {
        _this.form.recruitType = _this.recruitTypeList[0].dictValue;
        _this.form.isOverseas = _this.isOverseasList[1].dictValue;
        _this.form.recruitMethod = _this.recruitMethodList[0].dictValue;
      }
    });
  },
  mounted() {},
};
</script>
<style lang='scss' scoped>
.contentBox {
  .detailIcon {
    width: 98%;
    display: flex;
    flex-direction: row-reverse;
    img {
      width: 4vw;
      margin-right: 2vw;
    }
  }
  .appHomeContent {
    padding: 2vw;
    box-sizing: border-box;
    border: 0.5vw dashed #92bec5;
    border-radius: 1vw;
    width: calc(100% - 3vw);
    margin: 0.3vw 1.5vw 1vw;
    display: flex;
    flex-wrap: wrap;
    .contentItem {
      width: 100%;
      display: flex;
      margin-bottom: 2.42vw;
      position: relative;
      .itemLabel {
        width: 20%;
        margin-right: 0.9vw;
        padding-left: 1.8vw;
        font-size: 3.2vw;
        font-weight: 600;
        padding-left: 3vw;
        color: #44aaa9;
        position: relative;
        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #44aaa9;
        }
        // text-align-last: justify;
      }
      .itemValue {
        flex: 1;
        line-height: 4.8vw;
        background: #f5f8fd;
        font-size: 2.66vw;
        font-weight: 400;
        text-align: left;
        color: #868786;
        padding-left: 1vw;
        position: relative;
        .AddDate {
          line-height: 21px;
          &::after {
            display: block;
            content: "";
            border-width: 1vw;
            border-style: solid;
            border-color: #00eab3 transparent transparent transparent;
            position: absolute;
            right: 3%;
            top: calc(50% - 0.75vw);
          }
        }
        input {
          width: 100%;
          border: none;
          background: #f5f8fd;
          padding: 0.5vw 0;
        }
      }
      .w77 {
        max-width: 77vw;
      }
    }
    .itemLast {
      margin-bottom: 1vw;
    }
  }
}
</style>